import React, {  Suspense, lazy } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

const Sonn = lazy(() => import("./home/Sonn"));
const SonnHeimkoma = lazy(() => import("./home/SonnHeimkoma.js"));
const About = lazy(() => import("./pages/About"));
const Contact = lazy(() => import("./pages/Contact"));
const NoMAtch = lazy(() => import("./pages/404"));

const App = () => {
  return (
    <Router>
        <Suspense fallback={<div />}>
            <Routes>
                <Route path="/" element={<SonnHeimkoma />} />
                <Route path="/heim" element={<SonnHeimkoma />} />
                <Route path="/sonn" element={<Sonn />} />
                <Route path="/sonn-heimkoma" element={<SonnHeimkoma />} />
                <Route path="/about-us" element={<About />} />
                <Route path="/contact-us" element={<Contact />} />
                <Route path="*" element={<NoMAtch />} />
            </Routes>
        </Suspense>
      </Router>
  )
}

export default App